@import url(https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,400;1,700&family=Red+Hat+Display:wght@400;700&display=swap);
*, html {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #000;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
    background-color: #000000;
    font-family: 'Lato';
    color: #ffffff;
}
.Nav {
    background-color: #000000;
    position: fixed;
    width: 100%;
    height: auto;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    padding: 10px 0;
}

.Nav>.brand {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    margin-left: 40px;
    text-decoration: none;
}

.brand:active,
.brand:visited,
.brand:link,
.brand:hover {
    color: #fff;
}

.brand .brand__logo {
    max-height: 50px;
    margin-right: 10px;
}

.brand .brand__name {
    font-family: 'Red Hat Display';
    font-size: 32px;
    text-transform: uppercase;
    font-weight: 400;
}

.Nav>.links {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    margin-right: 30px;
    list-style: none;
}

.links>.links__link {
    font-family: 'Red Hat Display';
    font-weight: 400;
    font-size: 24px;
    margin: 0 10px;
}

.links>.links__link a {
    text-decoration: none;
    color: #ffffff;
    transition: all 0.2s;
}

.links>.links__link a:link,
.links>.links__link a:visited,
.links>.links__link a:active {
    text-decoration: none;
    color: #fff;
}

.links>.links__link>a:hover {
    color: #d91921;
}
.hero {
    padding-top: 100px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
}

.hero__title {
    font-size: 32px;
    font-family: 'Lato';
    text-transform: capitalize;
    margin-bottom: 20px;
}

.movie__poster {
    max-height: 60vh;
    max-width: 50vw;
    object-fit: contain;
    margin-bottom: 20px;
}

.movie__details {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    width: 50vw;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
}

.movie__details>ul {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    list-style: none;
    margin-bottom: 10px;
}

.movie__details>ul>li {
    font-family: 'Lato';
    font-size: 16px;
    margin: 5px 0;
}
